import { useEffect, useState } from "react"
import cn from "@lib/theming/createClassName"
import getWindow from "@lib/core/functions/getWindow"
import { createStylesHook } from "@fet/theming"
import Button from "@fet/controls/Button"
import Text from "@fet/Text"
import { useGameContext } from "./GameContext"

export function StartScreen() {
  const game = useGameContext()
  const [ classes ] = useStyles()
  const [ canStart, setCanStart ] = useState( false )

  useEffect( () => {
    let timeoutId = setTimeout( () => setCanStart( true ), 1000 * 1 )
    return () => { clearTimeout( timeoutId ) }
  }, [] )

  return (
    <section className={cn( classes.screen )}>
      <Text as="h1" body="Wycig Pana Spinacza z czasem" />
      <Text as="h2" body="CactuJam 11" />
      <Text>
        Pan Spinacz przey 10 lat. Ile lat dasz rad Ty przetrwa? <br />
        <s>Spoiler: przetrwanie 2 lat zakrawa o masochizm</s>
      </Text>
      <Text>
        Przygoda rozpoczyna si w spokojnej zimowej krainie, ktr zamieszkuj istoty takie jak Pan Spinacz. <br />
        To na jak dug przygod wyrusza zaley od Twoich umiejtnoci
      </Text>
      <Text>
        Strowanie Za pomoc WSAD lub strzaek. To wszystko. Powodzenia!
      </Text>
      <Button disabled={!canStart} onClick={() => game.start()} body="Rozpocznij gr" />
    </section>
  )
}

export function GameOverScreen({ distance, score }) {
  const game = useGameContext()
  const [ classes ] = useStyles()
  const [ canStart, setCanStart ] = useState( false )

  useEffect( () => {
    let timeoutId = setTimeout( () => setCanStart( true ), 1000 * 1 )
    return () => { clearTimeout( timeoutId ) }
  }, [] )

  return (
    <section className={cn( classes.screen )}>
      <Text as="h1" body="Wycig Pana Spinacza zakoczony" />
      <Text>
        Przetrwae <strong>{distance}</strong> dni.
        Uzyskae {score} punktw.
      </Text>
      <Button disabled={!canStart} onClick={() => getWindow()?.location.reload()} body="Restart" />
    </section>
  )
}

const useStyles = createStylesHook( ({ atoms }) => ({
  screen: {
    position: `absolute`,
    left: `50%`,
    top: `50%`,
    transform: `translate( -50%, -50% )`,
    width: `max-content`,
    height: `max-content`,
    display: `flex`,
    flexDirection: `column`,
    gap: atoms.spacing.main,
    alignItems: `center`,
    justifyContent: `center`,
    padding: atoms.spacing.main,
    backgroundColor: `#000A`,
    textAlign: `center`,
  },
}) )
